@import url(https://fonts.googleapis.com/css2?family=Public+Sans:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
html, body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Public Sans";
}

*{
  z-index: 1;
  font-family: "Public Sans";
  transition: all .2s linear;
}
.blur {
    filter: blur(3px);
    -webkit-filter: blur(3px);
    -moz-filter: blur(3px);
    -o-filter: blur(3px);
    -ms-filter: blur(3px);
}

@media only screen and (max-width: 768px) {
    .blur {
        filter: none;
        -webkit-filter: none;
        -moz-filter: none;
        -o-filter: none;
        -ms-filter: none;
    }
}
nav {
  height: 3.5rem;
  padding: 0px 2rem 0px 1.7rem;
  margin-bottom: 0.5rem;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.05);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.nav_back {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.back_text {
  font-size: 0.8rem;
}

.back_link,
.back_link:hover {
  color: #000000;
  font-weight: 600;
}

nav > div > ul {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: none;
}

nav > div > ul > li:first-child {
  background-color: rgba(39, 146, 62, 0.1);
  border-radius: 2px;
  font-size: 11px;
  padding: 0.4rem 1rem;
  color: #27923e;
  font-weight: 600;
  margin-right: 1.2rem;
  cursor: pointer;
}

nav > div > ul > li:nth-child(2) {
  font-size: 10px;
  margin: auto 0px auto 0px;
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
}

nav > div > ul > li:last-child {
  font-size: 6px;
  margin: auto 0px auto 10px;
  cursor: pointer;
}

li > img {
  width: 90%;
  border-radius: 50%;
}

li > svg {
  width: 0.5rem;
  height: 0.5rem;
}

.header_left {
  font-family: "Public Sans";
  font-weight: 600;
  font-size: 13px;
  display: block;
}

.header_right {
  justify-content: center;
  display: flex;
}

.profile_popup {
  padding: 0.7rem;
  background: #ffffff;
  position: absolute;
  right: 2rem;
  width: 8rem;
  font-size: 0.55rem;
  top: 3.1rem;
  z-index: 9;
}

.profile_popup > p {
  margin-bottom: 0.45rem;
}

.profile_popup > p:nth-child(1) {
  text-align: left;
  font-weight: 600;
}

.profile_popup > p:nth-child(2) {
  color: #27923e;
  padding-bottom: 1rem;
  border-bottom: 1px solid #e9e9e9;
}

.profile_popup > * {
  cursor: pointer;
}
@media only screen and (min-width: 768px) {
