.button, .button:disabled, .button[disabled]{
    width: 100%;
    background-color: #131321;
    color: #ffffff;
    font-size: 13px;
    border: 0px;
    border-radius: 4px;
    padding: 8px 7px;
    display: block;
    text-align: center;
    margin-top: 3rem;
}

.primary {
    background-color: #131321;
    color: #ffffff;
}

.primary:hover{
    color: #ffffff;

}

.default {
    background: #F4F8F9;
    color: #000000;
}

.secondary {
    color: #ffffff;
    background: #27923E;
}

.secondary:hover {
    color: #ffffff;
}

button>span>svg {
    stroke: #000;
}


.response_button>span>svg {
    stroke: #ffffff;
}

button>span:first-child{
    margin-right: 0px;
    width: 70%;
}

.btn_loading > svg {
        animation: rotation 1s infinite linear;
}